// 安全管理
<template>
  <div class="security">
    <div class="title">
      <h1 :style="{fontSize: '20px'}">{{preMnTitle}}</h1>
      <div class="search-edit">
        <el-date-picker
          size="small"
          v-model="time"
          type="year"
          value-format="yyyy"
          @change="changeTime"
          placeholder="选择日期时间">
        </el-date-picker>
        <el-button v-if="getUserJurisdiction('AT20')" :style="{marginLeft: '10px'}" size="small" type="primary" @click="clickAdd" icon="el-icon-plus"></el-button>
        <!-- <el-button :style="{marginLeft: '10px'}" size="small" @click="clickAdd" icon="el-icon-edit-outline"></el-button> -->
      </div>
    </div>
    <div class="table">
      <el-table :data="dataList" border strihostDomainpe header-cell-class-name="table-th">
        <el-table-column align="center" width="100px" prop="title" label="标题"></el-table-column>
        <el-table-column align="center" width="200px" prop="content" label="内容"></el-table-column>
        <el-table-column v-for="(item, index) in columns" :label="String(item.label)" :key="index" align="center">
          <el-table-column v-for="(item1, index1) in item.children" :key="index1" align="center"
            :label="String(item1.week)"
            :width="String(item.label).includes('/') ? '80px':'40px'"
            :prop="item.prop">
            <template slot-scope="scope">
              <span
                v-if="scope.row.safePlanDetailList && scope.row.safePlanDetailList[item1.week - 1] && scope.row.safePlanDetailList[item1.week - 1].safeId"
                class="record"
                :style="{
                  backgroundColor: scope.row.safePlanDetailList[item1.week - 1].status === 0 ? '#999999' :
                  scope.row.safePlanDetailList[item1.week - 1].status === 1 ? '#339933':
                  scope.row.safePlanDetailList[item1.week - 1].status === 2 ? '#0000FF' : '#FFF'
                }"
                @click="clickShowRecord(scope.row.safePlanDetailList[item1.week - 1], item1.week - 1)">
                {{scope.row.safePlanDetailList[item1.week - 1].safeId ? scope.row.safePlanDetailList[item1.week - 1].deviceType : ''}}
              </span>
            </template>
          </el-table-column>
        </el-table-column>
        <el-table-column fixed="right" align="center" width="140px" prop="content" label="操作">
          <template slot-scope="scope">
            <el-button size="small" v-if="getUserJurisdiction('AT21')" type="" @click="clickEdit(scope.row)" icon="el-icon-edit-outline"></el-button>
            <el-button size="small" v-if="getUserJurisdiction('AT22')" type="warning" @click="clickDelete(scope.row)" icon="el-icon-delete"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog title="安全演练记录表" @close="close('recordform')" :visible.sync="isRecordDialog" width="40%">
      <div class="main">
        <el-form :model="recordform" size="small" ref="recordform" label-width="100px">
          <el-row :gutter="20">
            <el-col :span="12">
              <!-- <el-form-item label="维保类型："><span>{{recordform.deviceType}}</span></el-form-item> -->
            </el-col>
            <el-col :span="12">
              <el-form-item label="演练人：">
                <el-input disabled v-model="recordform.submiter" placeholder=""></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="计划时间：">
                <el-input disabled v-model="recordform.firstDay" placeholder=""></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="执行时间：">
                <el-input disabled v-model="recordform.confirmTime" placeholder=""></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="演练内容：" prop="content" :rules="[{required: true, message: '请输入演练内容'}]">
            <el-input :disabled="recordform.status !== 0" type="textarea" :rows="2" v-model="recordform.content" placeholder="请输入演练内容"></el-input>
          </el-form-item>
          <el-form-item label="演练过程：" prop="flow" :rules="[{required: true, message: '请输入演练过程'}]">
            <el-input :disabled="recordform.status !== 0" type="textarea" :rows="2" v-model="recordform.flow" placeholder="请输入演练过程"></el-input>
          </el-form-item>
          <el-form-item label="演练结果：" prop="result" :rules="[{required: true, message: '请输入演练结果'}]">
            <el-input :disabled="recordform.status !== 0" type="textarea" :rows="2" v-model="recordform.result" placeholder="请输入演练结果"></el-input>
          </el-form-item>
          <el-form-item label="演练资料：">
            <div class="img">
              <img v-for="(item, index) in recordform.fileList"
                :key="index"
                :src="item ? hostDomain + item : ''" alt="">
              <el-upload
                v-if="recordform.status !== 2"
                class="avatar-uploader"
                :action="actionUrl"
                :show-file-list="false"
                :on-success="(res) => uploadSuccess(res)"
                :before-upload="(info) => beforeAvatarUpload(info, 3)">
                <i class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </div>
          </el-form-item>
          <el-form-item v-if="recordform.status == 2" label="确认人：">
            <el-input :style="{width: '50%'}" disabled v-model="recordform.confirmer" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" align="center">
        <el-button size="small" type="primary"
          v-if="recordform.status == 0 || (recordform.status === 1 && getUserJurisdiction('AT19'))"
          @click="clickRecordConfirm('recordform')">{{`${recordform.status == 0 ? '确认并提交' : '确认'}`}}</el-button>
      </div>
    </el-dialog>
    
    <el-dialog @close="close('addform')" :title="!isEdit ? '新建安全演练计划' : '编辑安全演练计划'" :visible.sync="isShowAddMaintain" width="40%">
      <div class="main">
        <el-form :model="addform" size="small" align="left" ref="addform" label-width="120px">
          <el-form-item label="标题：" prop="title" :rules="[{ required: true, message: '请输入标题' }]">
            <el-input v-model="addform.title"></el-input>
          </el-form-item>
          <el-form-item label="演练内容：" prop="content" :rules="[{ required: true, message: '请输入演练内容' }]">
            <el-input type="textarea" :rows="2" v-model="addform.content"></el-input>
          </el-form-item>
          <el-divider style="height:3px"></el-divider>
          <el-form-item label="安全演练周：" prop="week" :rules="[{ required: true, message: '请选择安全演练周' }]">
            <el-select :style="{width: '100%'}" v-model="addform.week" multiple placeholder="请选择">
              <el-option
                v-for="(item, index) in weekDataList"
                :key="index"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" align="center">
        <el-button size="small" type="primary" @click="clickAddMaintain('addform')">更新信息</el-button>
        <el-button size="small" @click="isShowAddMaintain = false">取消</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import config from "@/config";
import moment from "moment";
import jurisdiction from "@/mixin/jurisdictionMixin";

export default {
  mixins: [jurisdiction],
  data() {
    return {
      time: "",
      preMnTitle: "",
      isShowAddMaintain: false,
      isRecordDialog: false,
      isEdit: false,
      dataList: [1],
      recordform: {},
      actionUrl: "/pvams/upload",
      weekDataList: [],
      addform: {},
      columns: [],
      hostDomain: "",
      userId: "",
      realname: "",
      stationId: "",
    }
  },
  created() {
    this.hostDomain = `${config.HOST.pvamsDomain}`;
    this.userId = localStorage.getItem("userId");
    this.realname = localStorage.getItem("realname");
    const treeData = this.$store.state.getTreeId.treeId;
    if (treeData.id && treeData.id.includes("s")) {
      this.stationId = treeData.id.slice(1, treeData.id.length);
      if (this.stationId) {
        this.getTableData();
        this.getWeekData();
      }
    }
  },
  watch: {
    getStateData(v) {
      const stationId = this.$store.state.getTreeId.treeId.id;
      if (stationId && stationId.includes("s")) {
        this.stationId = stationId.slice(1, stationId.length);
        this.getTableData();
        this.getWeekData();
      }
    },
  },
  computed: {
    getStateData() {
      return this.$store.state.getTreeId.treeId;
    },
  },
  methods: {
    async getWeekData() {
      this.loadingOpen();
      const {data: { data }} = await this.axios('/pvams/getDateWeek');
      this.loadingClose();
      if (data && data.length !== 0) {
        const dataNum = data[data.length-1];
        if (dataNum && dataNum.length !== 0 && dataNum.children && dataNum.children.length !== 0) {
          const weekData = dataNum.children[dataNum.children.length - 1];
          const weekDay = weekData.week;
          for (let i = 1; i <= weekDay; i++) {
            const obj = { label: `第${i}周`, value: i};
            this.weekDataList.push(obj);
          }
        }
        data.forEach(v => {
          v['label'] = `${v.month}月`;
        })
        this.columns = data || [];
      }
    },
    async getTableData() {
      this.loadingOpen();
      const params = {stationId: this.stationId, year: this.time}
      const { data: {data: { list = [], title = "" }} } = await this.axios('/pvams/safe/getSafePlan', { params });
      this.loadingClose();
      this.preMnTitle = title;
      if (list) {
        this.dataList = list;
      }
    },
    close(formName) {
      this.$refs[formName].resetFields();
    },
    changeTime(v) {
      this.getTableData();
    },
    clickAdd() {
      this.isEdit = false;
      this.addform = {};
      this.isShowAddMaintain = true;
    },
    // 新增保养
    clickAddMaintain(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const params = {
            ...this.addform,
            userId: this.userId,
            year: this.time || '',
            stationId: this.stationId,
          };
          const url = this.addform.safeId ? "/pvams/safe/updateSafePlan" : "/pvams/safe/addSafePlan";
          const { data: { code } } = await this.axios.post(url, params);
          if (code === 200) {
            this.isShowAddMaintain = false;
            this.getTableData();
          }
        }
      })
    },
    async clickEdit(row) {
      this.isEdit = true;
      const { safeId = "" } = row;
      const { data: { code, data } } = await this.axios('/pvams/safe/query', { params: {safeId} });
      if (code === 200) {
        this.isShowAddMaintain = true;
        this.addform = {
          title: data.title || "",
          content: data.content || "",
          week: data.week || [],
          safeId: data.safeId || "",
        }
      }
    },
    clickDelete(row) {
      this.$confirm('此操作将永久删除, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(async () => {
        const { safeId = "" } = row;
        const params = { safeId };
        const { data: { code } } = await this.axios('/pvams/safe/delete', { params });
        if (code === 200) {
          this.$message({ type: 'success', message: '删除成功' });
          this.getTableData();
        }
      });
    },
    clickRecordConfirm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const { status = "" } = this.recordform;
          const params = {
            ...this.recordform,
            submitUserId: this.recordform.submitUserId || this.userId,
            confirmUserId: status === 0 ? null : this.userId,
          };
          const url = status === 0 ? "/pvams/safe/submitSafePlan" : "/pvams/safe/confirmSafePlan"
          const {data} = await this.axios.post(url, params);
          if (data.code === 200) {
            this.isRecordDialog = false;
            this.$message({ type: "success", message: "提交成功" });
            this.getTableData();
          }
        }
      });
    },
    /* 上传成功 */
    uploadSuccess(res) {
      if (res.code === 200) {
        this.$message({type: "success", message: "上传成功"});
        const { data } = res;
        if (this.recordform && !this.recordform.fileList) {
          this.recordform.fileList = [];
        }
        this.recordform.fileList.push(data);
      } else {
        this.$message({type: "error", message: res.message});
      }
    },
    // 上传前校验
    beforeAvatarUpload(file, type) {
      if (type === 1) {
        const isLt20M = file.size / 1024 / 1024 < 20;
        if (!isLt20M) this.$message.error('上传文件大小不得超过20mb!');
        return isLt20M;
      } else if (type === 2) {
        return true;
      } else if (type === 3) {
        const isLt10M = file.size / 1024 / 1024 < 10;
        const isJPGPng = file.type === 'image/jpeg' || file.type === 'image/png';
        if (!isJPGPng) this.$message.error('上传头像图片只能是 JPG或者Png 格式!');
        if (!isLt10M) this.$message.error('上传大小不得超过10mb!');
        return isJPGPng && isLt10M;
      }
    },
    clickShowRecord(row) {
      this.recordform = {
        ...row,
        submiter: row.submiter || this.realname,
      };
      this.recordform.confirmTime = row.status === 0 ? moment(new Date()).format('YYYY-MM-DD HH:mm:ss') : this.recordform.confirmTime;
      this.isRecordDialog = true;
    },
  },
}
</script>

<style lang="less" scoped>
.security {
  .img {
    display: flex;
    align-items: center;
    img {
      display: block;
      width: 136px;
      height: 95px;
    }
    .avatar-uploader {
      float: left;
      width: 136px;
      height: 95px;
      text-align: center;
      line-height: 95px;
      border: 1px solid #cccccc;
      border-radius: 5px;
      margin: 10px;
    }
  }
  .title {
    display: flex;
    justify-content: space-around;
    align-items: center;
    h1 {
      width: 70%;
      text-align: center;
    }
    .search-edit {
      width: 30%;
      display: flex;
      justify-content: flex-end;
      align-items: center;
      text-align: right;
    }
  }
  .table {
    margin-top: 20px;
    .record {
      display: block;
      width: 20px;
      height: 20px;
      line-height: 20px;
      text-align: center;
      border-radius: 2px;
      background-color: #001529;
      color: #ffffff;
      cursor: pointer;
    }
  }
}
</style>
<style lang="less">
.security {
    .img {
    .avatar-uploader {
      .el-upload {
        width: 100%;
      }
    }
  }
  .table-th {
    padding: 5px 0;
    background-color: #001529 !important;
    color: #ffffff;
  }
}
</style>